@use "../../assets/scss/colors" as *;
@use '../../assets/scss/mixins' as m;
@mixin center-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin padding($size) {
  padding-left: $size;
  padding-right: $size;
}
@mixin grid($rowCount, $spacing) {
  display: grid;
  grid-template-columns: repeat($rowCount, 1fr);
  grid-gap: $spacing;
  width: 100%;
}
.home {
  display: block;
  &-welcome-wrapper {
    h1 {
      padding-top: 2rem;
      color: $bluon-brand-blue;
      text-align: center;
      font-size: 2.1rem;
      padding-bottom: 2rem;
    }
  }
  &-video-container {
    @include center-flex;
    .home-video {
      width: 100%;
    }
  }
  &-impact {
    background: url("../../assets/images/ellipse-circle.png") no-repeat #f8f8f8;
    background-position: right;
    //height: 400px;
    @include padding(2.2rem);
    h1 {
      color: grey;
      font-size: 4.5vw;
      padding-bottom: 0.8rem;
      padding-top: 2rem;
    }
    h2 {
      font-size: 6vw;
      //width:70%;
    }
  }
  &-cards {
    @include center-flex;
  }
  &-app {
    background-color: $bluon-brand-blue;
    overflow-y: hidden;
    &-text {
      color: white;
      @include padding(2rem);
      .outer {
        padding-top: 4rem;
        width: 80%;
        padding-bottom: 2rem;
      }
      ul {
        padding-left: 2rem;
        padding-right: 2rem;
        list-style: circle;
        padding-bottom: 1.2rem;
        li {
          padding-bottom: 1rem;
          line-height: 1.5rem;
        }
      }
      //button will be changed
      button {
        color: black;
        background-color: white;
        border-radius: 50px;
        width: 130px;
        height: 45px;
      }
    }

    &-img {
      padding-top: 1.2rem;
      @include center-flex;
      transform: translateY(2rem);
      img {
        width: 50%;
        max-width:290px;
      }
    }
  }

  &-button-cards {
    padding-top: 0.5rem;
    @include m.team-grid(1);
    justify-content: center;
  }

  @media (min-width:786px){
    &-welcome-wrapper{
      h1{
        font-size: 3rem;
      }
    }
    &-impact{
      text-align: center;
      h1{
        font-size: 2vw;
      }
      h2{
        font-size: 4vw;
      }
    }
    &-app{
      display:flex; 
      justify-content: center;
      &-text{

      }
      &-img{
        img{
          width: 55%;
        }
      }
    }
    &-button-cards{
      @include center-flex(); 
    }
  }
  @media(min-width: 1028px){
    &-welcome-wrapper{
      h1{
        font-size: 4rem;
      }
      .home-video-container{
        
        video{
          width:65%; 
        }
      }
    }
    &-button-cards{
      //@include m.team-grid(3); 
      justify-content: space-between;
    }
    &-app{
      &-img{
        img{
          width:65%; 
        }
      }
    }
  }


}

